.popup-tooltip__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 430px;
    min-height: 330px;
    border-radius: 10px;
    background-color: #ffffff;
}

@media screen and (max-width: 910px) {
    .popup-tooltip__container {
        width: 280px;
        min-height: 308px;
    }
}